import React, { Component } from "react";

class Cookies extends Component {
  render() {
    return (
      <div>
        <div className="grid grid-column-box">
          <div className="box-container">
            <span className="">
              <h3>Informasjonskapsler (cookies)</h3>
              <p>
                Informasjonskapsler kan vre til stor hjelp og gi en bedre
                brukeropplevelse. www.amatte.no bruker Informasjonskapsler i
                sine nettlsninger.
              </p>
              <p>Det finnes to hovedtpyer av cookies:</p>
              <p className="no-margin">
                1. En permanent cookie ligger igjen p beskerens datamaskin for
                en bestemt tid.
              </p>
              <p>
                2. En sessioncookie lagres midlertidig i datamaskinens
                hukommelse i lpet av tiden en besker er inne p en nettside.
                Sessioncookies forsvinner nr du lukker din nettleser.
              </p>
            </span>
            <span className="">
              <h3 style={{ paddingTop: "15px" }}>Samtykke</h3>
              <p className="no-margin">
                Ved  benytte AMattes nettsider samtykker du i at vi kan
                etterlate informasjonskapsler i din nettleser. De fleste moderne
                nettlesere (Google Chrome, Internet Explorer, Firefox, Safari,
                Opera, etc.) er innstilt p  akseptere informasjonskapsler
                automatisk.
              </p>
              <p style={{ fontWeight: "700" }}>
                Dersom du nsker  deaktivere informasjonskapsler for din
                nettleser, kan du gjre dette via nettleserens
                sikkerhetsinnstillinger. Mer om fremgangsmte nederst p siden.
              </p>
            </span>
            <span className="">
              <h3 style={{ paddingTop: "15px" }}>
                Hva bruker AMatte informasjonskapsler til?
              </h3>
              <p>
                www.amatte.no benytter aldri informasjonskapsler i den hensikt 
                kartlegge enkeltbrukeres bruksmnster eller annen informasjon
                som vil kunne krenke personvernet.
              </p>
              <p>AMatte bruker informasjonskapsler til flgende forml:</p>
              <ul>
                <li>
                  For  lagre brukerinnstillinger - som f.eks visningssprk
                </li>
                <li>For  hndtere plogging og sesjoner</li>
              </ul>
            </span>
            <span className="">
              <h3 style={{ paddingTop: "15px" }}>
                Hvordan fjener man cookies?
              </h3>
              <p>
                Dersom du ikke lenger vil bruke cookies, kan du sl av cookies i
                innstillingene til din nettleser. Du kan ogs stille inn
                nettleseren slik at du blir spurt hver gang Enkeleksamens
                nettsiden prver  plassere en cookie p din datamaskin. Du kan
                ogs bruke nettleseren din til  slette tidligere lagrede
                cookies.
              </p>
              <p>
                Om du bruker PC, kan cookies fjernes ved hjelp av
                snarveiskommandoen [CTRL]+[SHIFT]+[Delete].
              </p>
              <p>
                Hvis det ikke fungerer eller hvis du bruker en MAC, klikk p
                linken for den nettleseren du bruker:
              </p>
              <ul>
                <li>
                  <a
                    target="_blank"
                    rel="noopener noreferrer"
                    href="https://support.mozilla.org/no/kb/slett-informasjonskapsler-fjerne-informasjon-netts"
                  >
                    Mozilla Firefox
                  </a>
                </li>
                <li>
                  <a
                    target="_blank"
                    rel="noopener noreferrer"
                    href="https://support.google.com/chrome/answer/95647?hl=no"
                  >
                    Google Chrome
                  </a>
                </li>
                <li>
                  <a
                    target="_blank"
                    rel="noopener noreferrer"
                    href="https://support.apple.com/no-no/guide/safari/sfri11471/mac"
                  >
                    Safari
                  </a>
                </li>
                <li>
                  <a
                    target="_blank"
                    rel="noopener noreferrer"
                    href="https://support.microsoft.com/nb-no/help/17442/windows-internet-explorer-delete-manage-cookies#ie=ie-11"
                  >
                    Internet Explorer
                  </a>
                </li>
                <li>
                  <a
                    target="_blank"
                    rel="noopener noreferrer"
                    href="https://help.opera.com/en/opera/Windows/1918/en/"
                  >
                    Opera
                  </a>
                </li>
              </ul>
              <p>
                Dersom du deaktiverer cookies vil deler til hele nettsidens
                funksjonalitet vre begrenset. Du kan for eksempel ikke
                gjennomfre et kjp eller logge inn.
              </p>
            </span>
          </div>
        </div>
      </div>
    );
  }
}

export default Cookies;
