import React, { Component } from "react";

class Privacy extends Component {
  render() {
    return (
      <div>
        <div className="grid grid-column-box">
          <div className="box-container">
            <span className="">
              <h4 style={{ textAlign: "center", paddingBottom: "10px" }}>
                Personvernerklring for AMatte.no
              </h4>
              <p>
                Denne personvernerklringen omhandler AMattes innsamling og bruk
                av personopplysninger som samles inn i forbindelse med bruk av
                nettsiden enkeleksamen.no eller ved registrering av bruker p
                www.amatte.no.
              </p>
              <p>
                AMatte er behandlingsansvarlig for opplysningene som samles inn
                ved bruk av www.amatte.no
              </p>
              <p>
                I denne personvernerklringen finner du informasjon om hvilke
                personopplysninger som samles inn, hvorfor vi gjr dette og dine
                rettigheter knyttet til behandlingen av personopplysningene.
                Denne erklringen vil kunne vre gjenstand for oppdateringer og
                endringer.
              </p>
              <p>
                Innholdet er identisk med det som til enhver tid er gjengitt som
                personvernerklring i vr internkontroll. Ved tvil er det
                internkontrollens versjon som er gjeldende. Sikkerhetsansvarlig
                i AMatte er ansvarlig for at personvernerklringen er ajourfrt
                ogs p nettsiden.
              </p>
            </span>
            <span className="">
              <h4 style={{ paddingTop: "15px" }}>1. Samling av informasjon</h4>
              <p>
                Vi samler data fra deg nr du registrerer p vr side, logger
                inn p din konto, gjr et kjp, deltar i en konkurranse og/eller
                nr du logger ut. Den samlede informasjonen inkluderer ditt
                navn, epost adresse, telefonnummer og/eller kredittkort.
              </p>
              <p>
                I tillegg, s mottar vi og registrerer automatisk informasjon om
                din datamaskin og nettleser, inklusiv din IP adresse,
                programvare og maskinvare, samt siden du etterspr.
              </p>
            </span>
            <span className="">
              <h4 style={{ paddingTop: "15px" }}>2. Bruk av informasjon</h4>
              <p>
                Vi samler inn og bruker dine personopplysninger til ulike forml
                avhengig av hvem du er og hvordan vi kommer i kontakt med deg.
                Vi behandler personopplysninger som leverandr av tjenester, i
                markedsfringsyemed og i forbindelse med besk p vr
                hjemmeside, www.amatte.no.
              </p>
              <p>All informasjon vi samler fra deg kan bli brukt for :</p>

              <ul>
                <li>
                  Tilpasse din opplevelse og svare for dine individuelle behov
                </li>
                <li>Forbedre vr nettside</li>
                <li>Forbedre kundesttten og dine behov</li>
                <li>Kontakte deg via epost</li>
              </ul>
              <h5>Informasjonskapsler</h5>
              <p>
                Informasjonskapsler (cookies) er sm tekstfiler som plasseres
                p din datamaskin, mobiltelefon eller nettbrett nr du laster
                ned en nettside. Informasjonskapslene kan eksempelvis inneholde
                brukerinnstillinger og informasjon om hvordan du har surfet p
                og brukt vr nettside, hvilken nettleser du bruker og lignende.
              </p>
              <p>
                Vi benytter forskjellige typer informasjonskapsler p amatte.no.
                Disse kan deles inn i flgende kategorier:
              </p>
              <ul>
                <li>
                  Vi bruker informasjonskapsler for  forbedre nettstedet og
                  forenkle din brukeropplevelse.
                </li>
                <li>
                  Vi bruker informasjonskapsler fra tredjepartsverkty for
                  analyse og statistikk.
                </li>
                <li>
                  Vi bruker informasjonskapsler fra ulike
                  annnonseringsplattformer, for  ke effektiviteten av vr
                  digitale markedsfring og spore brukerregistreringer.
                </li>
              </ul>
              <p>
                Dersom du ikke nsker at vi skal benytte informasjonskapsler til
                 samle inn data, s kan du endre sikkerhetsinnstillingene for
                informasjonskapsler p din datamaskin, mobiltelefon eller
                nettbrett.
              </p>
            </span>
            <span className="">
              <h4 style={{ paddingTop: "15px" }}>4. E-handels personvern</h4>
              <p>
                Vi er eneeier av informasjonen samlet p denne siden. Din
                personlige identifiserbare informasjon vil ikke bli solgt,
                utvekslet, overfrt eller gitt til andre selskaper uansett
                rsak, uten din tillatelse, utenom det som er ndvendig for 
                fullfre en foresprsel og/eller transaksjon, f.eks for  skipe
                en ordre.
              </p>
            </span>
            <span className="">
              <h4 style={{ paddingTop: "15px" }}>5. Tredjeparts avslring</h4>
              <p>
                Vi selger, bytter eller overfrer ikke din personlige
                identifiserbare informasjon til utestende parter. Dette
                inkluderer ikke klarerte tredjeparter som assisterer oss i
                administrering av vr nettside eller gjennomfring av vr
                virksomhet, s lenge partene er enige i  holde denne
                informasjonen konfidensiell.
              </p>
              <p>
                Vi mener det er ndvendig  dele informasjon for  etterforske,
                forhindre eller handle mot ulovlige aktiviteter, mistanke p
                svindel, situasjoner som involverer potensiell risiko p en
                persons fysiske sikkerhet, brudd p vre vilkr for bruk eller
                dersom det er pkrevd ved lov.
              </p>
            </span>
            <span className="">
              <h4 style={{ paddingTop: "15px" }}>6. Informasjonsbekyttelse</h4>
              <p>
                Vi implementerer en rekke sikkerhetstiltak for  opprettholde
                sikkerheten p din personlige informasjon. Vi bruker den beste
                tilgjengelige krypteringen for  beskytte sensitiv informasjon
                overfrt p nett. VpnMentor beskytter ogs din informasjon
                offline. Kun ansatte som behver  utfre en spesifikk jobb (for
                eksempel fakturering eller kundesttte) fr tilgang p personlig
                identifiserbar informasjon. Datamaskinene/serverne som brukes
                for  lagre personlig identifiserbar informasjon holdes i et
                sikkert milj.
              </p>
              <h5>Bruker vi cookies?</h5>
              <p>
                Ja. Vre cookies forbedrer tilgangen til vr side og
                identifiserer repeterende beskende. Dessuten, s forbedrer vre
                cookies opplevelsen for brukeren ved  spore og rette seg mot
                han/hennes interesser. Denne cookie bruken er dog ikke p noen
                mte tilknyttet personlig identifiserbar informasjon p vr
                side.
              </p>
            </span>
            <span className="">
              <h4 style={{ paddingTop: "15px" }}>7. Avslutte abonnement</h4>
              <p>
                Vi bruker epost adressen du gir oss for  sende deg informasjon
                og oppdateringer knyttet til din ordre, sporadiske firmanyheter,
                relatert produktinformasjon osv. Hvis du nsker  avslutte
                abonnementet p vre eposter, s inkluderer vi detaljert
                informasjon om hvordan dette gjres i hver epost.
              </p>
            </span>
            <span className="">
              <h4 style={{ paddingTop: "15px" }}>8. Kontaktinformasjon</h4>
              <p>
                Du kan nr som helst stille oss sprsml om hvordan vi behandler
                dine brukerdata og gjre dine ovennevnte rettigheter gjeldende,
                ved  kontakte vr Kundeservice p e- post:
                kundeservice@amatte.no.
              </p>
            </span>
            <span className="">
              <h4 style={{ paddingTop: "15px" }}>9. Enighet</h4>
              <p>
                Ved  bruke vr nettside, s er du enig i vr
                personvernserklring.
              </p>
            </span>
          </div>
        </div>
      </div>
    );
  }
}

export default Privacy;
